*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  height: 100%; }

body {
  margin: 0;
  min-width: 768px;
  font-family: "Gilroy", "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #242424;
  background-color: #ffffff; }

main {
  flex: 1 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.hidden {
  display: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  color: inherit;
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 19px 45px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  border-radius: 52px; }
  .button::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: linear-gradient(98.61deg, #fbdb6a 5.19%, #ffeba3 49.97%, #fbdb6a 95.49%);
    box-shadow: inset 0 0 26px rgba(255, 255, 255, 0.54);
    border-radius: 52px; }
  .button::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: -3px;
    z-index: -1;
    width: 100%;
    background: #e8c240;
    border-radius: 52px; }

.header {
  position: relative;
  display: flex;
  justify-content: center;
  padding: 62px 0 50px; }

.header__logo {
  display: flex;
  align-items: flex-end;
  font-weight: 300;
  font-size: 13px;
  line-height: 18px;
  color: #242424; }
  .header__logo img {
    width: 173px;
    height: 40px;
    margin-right: 15px; }
  .header__logo span {
    max-width: 235px; }
  .header__logo:hover, .header__logo:active {
    opacity: 0.8;
    outline: none; }

.main {
  width: 100%;
  max-width: 1002px;
  padding: 0 20px;
  margin-left: auto;
  margin-right: auto; }

.methods {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  padding: 0 0 50px 35px; }

.methods__item {
  display: flex;
  align-items: flex-start;
  width: 33.33333%;
  padding: 0 20px 0 20px; }
  .methods__item:nth-child(3n) .methods__text {
    max-width: 140px; }

.methods__icon {
  position: relative;
  flex-shrink: 0;
  width: 60px;
  height: 60px;
  margin-right: 10px;
  border: 0.5px solid rgba(80, 181, 32, 0.09);
  box-shadow: inset 0 10px 20px rgba(255, 255, 255, 0.25);
  border-radius: 50%; }
  .methods__icon::before, .methods__icon::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 50%;
    transform: translate(-50%, -50%); }
  .methods__icon::before {
    width: 47.37px;
    height: 47.37px;
    border: 1px solid rgba(80, 181, 32, 0.09);
    box-shadow: inset 0 10px 20px rgba(255, 255, 255, 0.25); }
  .methods__icon::after {
    width: 36.32px;
    height: 36.32px;
    background-color: #50b520;
    box-shadow: inset 0 10px 20px rgba(255, 255, 255, 0.25); }
  .methods__icon svg {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1;
    transform: translate(-50%, -50%); }

.methods__text strong {
  display: block;
  padding-right: 15px;
  font-weight: 800; }

.question {
  display: none;
  flex-direction: column;
  align-items: center;
  min-height: 388px;
  padding: 30px;
  border: 1px dashed rgba(0, 0, 0, 0.08);
  filter: drop-shadow(0px 0px 30px rgba(0, 0, 0, 0.06));
  border-radius: 8px; }
  .question--show {
    display: flex; }
  .question label {
    cursor: pointer; }
  .question--number label {
    max-width: 260px;
    margin-bottom: 38px;
    text-align: center;
    font-size: 24px;
    line-height: 29px; }
    .question--number label b {
      font-weight: 800;
      color: #50b520; }
  .question--number input {
    padding: 0;
    margin: 0;
    background-color: transparent;
    border: none;
    border-radius: 0;
    outline: none;
    appearance: none;
    width: 210px;
    height: 61px;
