    border-radius: 4px; }
    .question--number input::-webkit-input-placeholder {
      color: rgba(0, 0, 0, 0.45); }
    .question--number input::-moz-placeholder {
      color: rgba(0, 0, 0, 0.45); }
    .question--number input:-ms-input-placeholder {
      ccolor: rgba(0, 0, 0, 0.45); }

.question__number {
  display: block;
  margin-bottom: 15px;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
